.Loading-content {
  padding: 0px 15px 20px 15px;
  text-align: center;
}

.Loading-error {
  border-radius: 50%;
  border: 8px solid red;
  color: red;
  content: "!";
  font-size: 30px;
  font-weight: bold;
  height: 60px;
  margin: 0 auto 10px;
  width: 60px;
}

.Loading-spinner {
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  border-radius: 50%;
  border: 8px solid #f3f3f3;
  border-top: 8px solid #3c3c3c;
  height: 60px;
  margin: 0 auto 10px;
  width: 60px;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


body {
  font-size: 16px;
}

.btn {
  font-size: 20px;
  min-height: 44px;
}

.navbar-brand {
  font-size: 20px;
}

.form-control {
  font-size: 16px;
}

.blinking-cursor{
  -webkit-animation: 1s blink step-end infinite;
          animation: 1s blink step-end infinite;
  font-weight: 100;
}

@-webkit-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: inherit;
  }
}

@keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: inherit;
  }
}

